html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

a {
  background-color: transparent;
}

small {
  font-size: 80%;
}

img {
  border-style: none;
}

button, input, optgroup, select, textarea {
  font-family: inherit; /* 1 */
  font-size: 100%;  /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
}

button, input { /* 1 */
  overflow: visible;
}

button, select { /* 1 */
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

details {
  display: block;
}

template, [hidden] {
  display: none;
}
